export function isTel (phone) {
  return /^1[3-9]\d{9}$/g.test(phone)
}

export function isCode (code) {
  return /^\d{4}$/g.test(code)
}

export function setCookie (name, value, expiredays) {
  let exdate = new Date()
  exdate.setTime(Number(exdate) + expiredays)
  document.cookie = name + '=' + value + ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
}

export function getCookie (name) {
  if (document.cookie.length > 0) {
    let start = document.cookie.indexOf(name + '=')
    if (start !== -1) {
      start = start + name.length + 1
      let end = document.cookie.indexOf(';', start)
      if (end === -1) end = document.cookie.length
      return document.cookie.substring(start, end)
    }
  }
  return ''
}
