const Resource = require('./create-api')
const utils = require('../libs/utils')
const stringify = require('qs').stringify
const { api } = Resource

export function getSmsToken () {
  return api.post('/smart-piano/v4/user/sms_token')
}

export function getSmsCode (params = {}) {
  return api.post('/smart-piano/v4/user/send_verification_code', stringify(params))
}

export function bindMobile (params = {}) {
  return api.post('/smart-piano/v4/user/sms_login', stringify(params))
}

export function searchScore (scoreName, params = {}) {
  let token = utils.getCookie('TOKEN')
  console.log(token)
  return api.get(`/smart-piano/v4/search/${scoreName}`, {
    params: params,
    headers: {
      'TOKEN': token
    }
  })
}

export function scoreScore (params = {}) {
  let token = utils.getCookie('TOKEN')
  return api.post('/smart-piano/v4/user/favor', stringify(params), {
    headers: {
      'TOKEN': token
    }
  })
}
