<template>
  <div class="container">
    <p class="title">绑定手机</p>
    <div class="input-zone tel-zone">
      <input type="text" placeholder="请输入手机号" name="user_mobile" maxlength="13" ref="mobile" @focus="handleInput" @input="formatTel">
    </div>
    <div class="input-zone code-zone">
      <input type="text" v-model="verifyCode" placeholder="请输入验证码" name="user_code" maxlength="4" ref="code">
      <span class="code-btn" :class="{'code-btn-disabled': !(mobile)}" @click="getCode">{{text}}</span>
    </div>
    <button class="bind-btn" @click="handleBind">点击绑定</button>
  </div>
</template>
<script>
import { getSmsToken, getSmsCode, bindMobile } from 'api'
import { Toast } from 'mint-ui'
import { isTel, setCookie } from '@/libs/utils.js'

export default {
  data () {
    return {
      mobile: '',
      text: '获取验证码',
      smsToken: '',
      code: '',
      verifyCode: ''
    }
  },
  methods: {
    isTel () {
      if (!this.mobile) {
        Toast('请输入手机号')
        return
      }
      if (!isTel(this.mobile)) {
        Toast('手机号格式错误')
        return
      }
      return true
    },
    isCode () {
      if (!this.verifyCode) {
        Toast('请输入验证码')
        return
      }
      return true
    },
    async getCode (ev) {
      let el = ev.target
      const self = this
      if (!this.isTel()) {
        return
      }
      if (el._disabled) return
      el._disabled = true
      try {
        let token = (await getSmsToken()).data.data.token
        let params = {
          access_token: token,
          mobile: this.mobile
        }
        await getSmsCode(params)
        el.setAttribute('disabled', true)
        let endTime = new Date().getTime() + 90 * 1000
        ;(function counter () {
          if (endTime - new Date().getTime() <= 0) {
            el._disabled = false
            self.text = '获取验证码'
            el.removeAttribute('disabled')
            return
          }
          self.text = `${Math.ceil((endTime - new Date().getTime()) / 1000)}s`
          setTimeout(() => {
            counter()
          }, 1000)
        })()
      } catch (err) {
        Toast(err.meta.message || '获取验证码失败')
      } finally {
        el._disabled = false
      }
    },
    async handleBind () {
      if (!this.isTel() || !this.isCode()) {
        return
      }
      let params = {
        mobile: this.mobile,
        code: this.$refs.code.value
      }
      try {
        let callback = await bindMobile(params)
        console.log(callback)
        let token = callback.data.data.token
        setCookie('TOKEN', token, 3 * 24 * 60 * 1000)
        this.$router.push('/')
      } catch (err) {
        console.log(err)
        // Toast(err.meta.message || '绑定失败')
        Toast('绑定失败')
      } finally {
        console.log('sss')
      }
    },
    handleInput () {
      console.log('click')
    },
    formatTel (e) {
      // 获取当前光标的位置
      let position = e.target.selectionStart
      // // 获取当前的value
      let value = e.target.value
      // 从左边沿到坐标之间的空格数
      let sp = (value.slice(0, position).match(/\s/g) || []).length
      // // 去掉所有空格
      let nospace = value.replace(/\s/g, '')
      // 重新插入空格
      let t1 = nospace.substring(0, 3).replace(/^(\w{3})/g, '$1 ')
      let t2 = nospace.substring(3).replace(/^(\w{4})/g, '$1 ')
      let curVal = e.target.value = t1 + t2
      this.mobile = nospace
      // // 从左边沿到原坐标之间的空格数
      let curSp = (curVal.slice(0, position).match(/\s/g) || []).length
      // // 修正光标位置
      e.target.selectionEnd = e.target.selectionStart = position + curSp - sp
    }
  }
}
</script>
